// src/components/FinancialMode.js
import React, { useState } from 'react';

const FinancialMode = () => {
  const [principal, setPrincipal] = useState('');
  const [rate, setRate] = useState('');
  const [time, setTime] = useState('');
  const [compoundInterest, setCompoundInterest] = useState(null);

  // Function to calculate compound interest
  const calculateCompoundInterest = () => {
    const p = parseFloat(principal);
    const r = parseFloat(rate) / 100;
    const t = parseFloat(time);
    if (p && r && t) {
      const ci = (p * Math.pow(1 + r, t)).toFixed(2);
      setCompoundInterest(ci);
    }
  };

  return (
    <div className="p-4 bg-gray-800 rounded-lg text-gold">
      <h2 className="text-xl font-semibold mb-4">Financial Mode</h2>

      <div className="mb-4">
        <label className="block text-sm mb-2">Principal Amount:</label>
        <input
          type="number"
          value={principal}
          onChange={(e) => setPrincipal(e.target.value)}
          className="w-full p-2 rounded-lg bg-gray-700 text-white"
        />
      </div>

      <div className="mb-4">
        <label className="block text-sm mb-2">Rate of Interest (%):</label>
        <input
          type="number"
          value={rate}
          onChange={(e) => setRate(e.target.value)}
          className="w-full p-2 rounded-lg bg-gray-700 text-white"
        />
      </div>

      <div className="mb-4">
        <label className="block text-sm mb-2">Time Period (years):</label>
        <input
          type="number"
          value={time}
          onChange={(e) => setTime(e.target.value)}
          className="w-full p-2 rounded-lg bg-gray-700 text-white"
        />
      </div>

      <button
        onClick={calculateCompoundInterest}
        className="button"
      >
        Calculate Compound Interest
      </button>

      {compoundInterest && (
        <div className="mt-4">
          <h3 className="text-lg font-semibold">Compound Interest: {compoundInterest}</h3>
        </div>
      )}
    </div>
  );
};

export default FinancialMode;
