import React from 'react';

const ScientificMode = ({ handleInput, calculateResult }) => {
  return (
    <>
      {['sin', 'cos', 'tan', 'log'].map((func) => (
        <button key={func} onClick={() => handleInput(`${func}(`)} className="button">{func}</button>
      ))}
      <button onClick={() => handleInput('^')} className="button">^</button>
      <button onClick={() => handleInput('sqrt(')} className="button"></button>
      <button onClick={() => handleInput('%')} className="button">%</button>
      <button onClick={() => handleInput('(')} className="button">(</button>
      <button onClick={() => handleInput(')')} className="button">)</button>
      <button onClick={calculateResult} className="button col-span-2 bg-gold text-black">=</button>
    </>
  );
};

export default ScientificMode;
