// src/components/MedicalMode.js
import React, { useState } from 'react';

const MedicalMode = () => {
  const [weight, setWeight] = useState('');
  const [height, setHeight] = useState('');
  const [bmi, setBmi] = useState(null);

  // Function to calculate BMI
  const calculateBMI = () => {
    const weightInKg = parseFloat(weight);
    const heightInM = parseFloat(height) / 100;
    if (weightInKg && heightInM) {
      const bmiResult = (weightInKg / (heightInM * heightInM)).toFixed(2);
      setBmi(bmiResult);
    }
  };

  return (
    <div className="p-4 bg-gray-800 rounded-lg text-gold">
      <h2 className="text-xl font-semibold mb-4">Medical Mode</h2>

      <div className="mb-4">
        <label className="block text-sm mb-2">Weight (kg):</label>
        <input
          type="number"
          value={weight}
          onChange={(e) => setWeight(e.target.value)}
          className="w-full p-2 rounded-lg bg-gray-700 text-white"
        />
      </div>

      <div className="mb-4">
        <label className="block text-sm mb-2">Height (cm):</label>
        <input
          type="number"
          value={height}
          onChange={(e) => setHeight(e.target.value)}
          className="w-full p-2 rounded-lg bg-gray-700 text-white"
        />
      </div>

      <button
        onClick={calculateBMI}
        className="button"
      >
        Calculate BMI
      </button>

      {bmi && (
        <div className="mt-4">
          <h3 className="text-lg font-semibold">BMI: {bmi}</h3>
        </div>
      )}
    </div>
  );
};

export default MedicalMode;
