import React from 'react';

const BasicMode = ({ handleInput, calculateResult, clearDisplay }) => {
  return (
    <>
      {['7', '8', '9', '/'].map((item) => (
        <button key={item} onClick={() => handleInput(item)} className="button">{item}</button>
      ))}
      {['4', '5', '6', '*'].map((item) => (
        <button key={item} onClick={() => handleInput(item)} className="button">{item}</button>
      ))}
      {['1', '2', '3', '-'].map((item) => (
        <button key={item} onClick={() => handleInput(item)} className="button">{item}</button>
      ))}
      {['0', '.', '=', '+'].map((item) => (
        <button
          key={item}
          onClick={() => item === '=' ? calculateResult() : handleInput(item)}
          className="button">{item}
        </button>
      ))}
      <button onClick={clearDisplay} className="button col-span-2">Clear</button>
    </>
  );
};

export default BasicMode;
