import React, { useState } from 'react';
import './App.css';
import BasicMode from './components/BasicMode';
import ScientificMode from './components/ScientificMode';
import MedicalMode from './components/MedicalMode';
import FinancialMode from './components/FinancialMode';


function App() {
  const [mode, setMode] = useState('Basic');
  const [display, setDisplay] = useState('');
  const [history, setHistory] = useState([]);

  // Function to handle basic calculations
  const calculateResult = () => {
    try {
      const result = eval(display); // Replace eval with more secure calculation logic for production
      setDisplay(result);
      setHistory([...history, `${display} = ${result}`]);
    } catch (e) {
      setDisplay('Error');
    }
  };

  // Function to handle input and clear display
  const handleInput = (value) => setDisplay((prev) => prev + value);
  const clearDisplay = () => setDisplay('');

  return (
    <div className="calculator bg-slate-900 min-h-screen flex flex-col items-center justify-center text-gold p-4">
      <div className="w-full max-w-md bg-gray-800 rounded-lg shadow-lg p-6">
        <div className="display bg-gray-900 text-right text-2xl font-semibold text-gold p-4 rounded-lg mb-4">{display || '0'}</div>

        {/* Mode Toggle */}
        <div className="flex space-x-2 mb-4">
          {['Basic', 'Scientific', 'Medical', 'Financial'].map((item) => (
            <button
              key={item}
              className={`py-2 px-4 rounded-lg ${mode === item ? 'bg-gold text-black' : 'bg-gray-700 text-gold'}`}
              onClick={() => setMode(item)}
            >
              {item}
            </button>
          ))}
        </div>

        {/* Mode Components */}
        <div className="button-grid grid grid-cols-4 gap-2">
          {mode === 'Basic' && <BasicMode handleInput={handleInput} calculateResult={calculateResult} clearDisplay={clearDisplay} />}
          {mode === 'Scientific' && <ScientificMode handleInput={handleInput} calculateResult={calculateResult} />}
          {mode === 'Medical' && <MedicalMode handleInput={handleInput} calculateResult={calculateResult} />}
          {mode === 'Financial' && <FinancialMode handleInput={handleInput} calculateResult={calculateResult} />}
        </div>
      </div>

      {/* History Display */}
      <div className="history mt-4 w-full max-w-md bg-gray-800 rounded-lg p-4 text-sm text-gold">
        <h3 className="text-center mb-2 font-semibold">Calculation History</h3>
        <ul>
          {history.slice(-5).map((entry, index) => (
            <li key={index} className="py-1">{entry}</li>
          ))}
        </ul>
      </div>
    </div>
  );
}

export default App;
